@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&family=Outfit:wght@200;300;400;500;600;700&family=Roboto:wght@400;700&display=swap');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}

body {
  background: black;
}

h1,
h4,
p,
a {
  color: white;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn:hover {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(62, 15, 248);
  color: #fcfcfc;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
  border-radius: 10px;
}

.btn-light {
  /*background: rgba(255, 255, 255, 0.2);*/
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(89, 48, 201, 0.2);
  color: #fff;
  transition: 0.3s;
}

@media screen and (max-width:765px) {
  .btn {
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background: rgb(62, 15, 248);
    color: #fcfcfc;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
    border-radius: 10px;
  }
  
}
